.img {
  width: 50%;
  height: 50%;
  object-fit: cover;
}

.card {
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  transition: 0.3s;
  border-radius: 5px;
  background-color: orange;
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}

.title {
  font-size: 20px;
  font-weight: bold;
  padding: 10px;
  text-align: center;
  color: white;
  background-color: black;
  border-radius: 0 0 5px 5px;
}
